/***************************************************/
/* BoardList */


/* header */
.header{
  /* padding: 20px 0; */
  text-align: center;
  width: 500px;
  height: 30px;
  margin: 30px auto;
  
  display: flex;
  justify-content: space-between;
}
  
/* 메인 검색창 */
.header input{
  width: 300px;
  margin: 0 10px;
}

/* 메인 버튼 */
.header button{
  width: 80px;
  font-size: 14px;
  font-weight: bold;
  background-color: #455ba8;
  border: none;
  color: white;
  cursor: pointer;
}

.header  button:hover{
  background-color: white;
  color : #455ba8;
  border: 2px solid #455ba8;
}

/* 메인 목록 */
.list{
  padding: 20px;
  text-align: left;
  border-bottom: 1px solid grey;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* 메인 목록 정보 */
.list .content{
  flex-basis: 100%;
}

.list p{
  padding-top: 5px;
}


/* 메인 목록 버튼영역 */
.list .board-btn-area{
  flex-basis: 15%;
}

/* 메인 목록 버튼 */
.list .board-btn-area > button{
  width: 80px;
  height: 30px;

  font-size: 15px;
  font-weight: bold;
  border: 0;
  background-color: white;
  color : #455ba8;
  border: 2px solid #455ba8;
  cursor: pointer;
}

*::selection {
  background-color: transparent; /* 선택된 영역의 배경색을 투명하게 설정 */
  color: inherit; /* 선택된 텍스트의 색상을 부모 요소의 색상으로 설정 */
}

.content > h4 > span{
  cursor: pointer;
}

/* 모달 */
.modal{
  height: 200px;
  margin-top : 20px;
  padding : 20px;
  background : #ccc;
  text-align : left;
}

/* 시크릿링크 */
.secretLink{
  color: white;
  cursor: default;
}